import pageStatus from '../../components/status/status.vue';
import shopsHeader from './shopsHeader.vue';
import infoModel from './infoModel.vue';
import activity from './activity.vue';
import goods from './goods.vue';
import goodsLists from './goodsLists.vue';
import shopCart from './shopCart.vue';
import interfaces from '../../utils/interfaces.js';

export default {
	data() {
		return {
			shopInfo: null,
			showInfoModel: false,
			tabNav: [{ name: '点餐' }, { name: '评价' }, { name: '商家' }],
			currentIndex: 0,
			scrollTop: 0,
			statusBarHeight: 0,
			showStatus: false
		};
	},
	onLoad(option) {
		// console.log(option)
		if (!option.shop_id) {
			uni.switchTab({
				url: '../tabBar/home/home'
			});
			return;
		}
		this.initData();
	},
	watch: {









	},
	mounted() {






	},
	components: {
		pageStatus,
		shopsHeader,
		infoModel,
		activity,
		goods,
		goodsLists,
		shopCart
	},
	onPageScroll(e) {



	},
	methods: {
		initData() {
			this.request({
				url: interfaces.getShops,
				success: res => {
					this.shopInfo = res;
				}
			});
		},
		clickNav(index) {
			this.currentIndex = index;
		},
		scroll() {}
	}
};
